import React, { Component } from "react";
import classes from "./Silver.css";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import img from "../../../assets/images/desktop/Apple_iPhone_8_Silver_WS2-full-product-front.png";
//this.props.silver
class Silver extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className={classes.Silver}>
        <img src={img} width="255" alt="Silver" />
      </div>
    );
  }
}

const mapStateToProps = state => {
  console.log("stateeeeee1111 ", state.phone.phones);
  console.log("state.phone.loadinggg ", state.phone.loading);
  return {
    silverPhone64GB: state.phone.silverPhone64GB,
    loading: state.phone.loading
  };
};
export default withRouter(connect(mapStateToProps)(Silver));



