import React, { Component } from "react";
import classes from "./Gold.css";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";

import img from "../../../assets/images/desktop/Apple_iPhone_8_Gold-full-product-front.png";
//this.props.gold
class Gold extends Component {
  constructor(props) {
    super(props);
    this.state = {
      phone: {
        imageUrl: ""
      }
    };
  }
  componentDidMount = () => {
    const { value } = this.props.goldPhone64GB[0].merchandisingMedia[0];
    const image_url = `../../../assets${value}`;
    this.setState({ phone: { imageUrl: image_url } }, () => {});
  };
  render() {
    if (this.state.phone.imageUrl === "") {
      return <div />;
    }
    console.log("tthis.state.phone.imageUrl ", this.state.phone.imageUrl);
    return (
      <div className={classes.Gold}>
        <img src={this.state.phone.imageUrl} width="255" alt="Gold" />
      </div>
    );
  }
}

const mapStateToProps = state => {
  console.log("stateeeeee1111 ", state.phone.phones);
  console.log("state.phone.loadinggg ", state.phone.loading);
  return {
    goldPhone64GB: state.phone.goldPhone64GB,
    loading: state.phone.loading
  };
};
export default withRouter(connect(mapStateToProps)(Gold));



