import React, { Component } from "react";
import classes from "./SpaceGrey.css";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import img from "../../../assets/images/desktop/Apple_iPhone_8_Space_Grey_WS2-full-product-front.png";
//this.props.spaceGrey

class SpaceGrey extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className={classes.SpaceGrey}>
        <img src={img} width="255" alt="SpaceGrey" />
      </div>
    );
  }
}
const mapStateToProps = state => {
  console.log("stateeeeee1111 ", state.phone.phones);
  console.log("state.phone.loadinggg ", state.phone.loading);
  return {
    spacegreyPhone64GB: state.phone.spacegreyPhone64GB,
    loading: state.phone.loading
  };
};
export default withRouter(connect(mapStateToProps)(SpaceGrey));



