import React, { Component } from "react";
import classes from "./PhonePricesPanel.css";
import PriceBox from "../../../UI/Panels/PriceBox/PriceBox";

export default class PhonePricesPanel extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    console.log("this.props.oneOffPriceeeeee ", this.props.oneOffPrice);
    console.log("this.props.monthlyPrice ", this.props.monthlyPrice);
    return (
      <div className={classes.PhonePricesPanel}>
        <PriceBox
          preText="from"
          price={`£${this.props.oneOffPrice}`}
          postText="upfront cost"
        />
        <p />
        <PriceBox
          preText="When you pay"
          price={`£${this.props.monthlyPrice}`}
          postText="a month"
        />
      </div>
    );
  }
}



